.consultation {
	min-height: 680px;
	padding: 100px 0 92px 0;
	background: url('../img/consultation.jpg') center (center/cover) no-repeat;
	&__descr {
		font-weight: 500;
		font-size: 20px;
		line-height: 30px;
		color: #FFFFFF;
	}
	.error {
		border: 1px solid red;
	}
	label.error {
		color: #ffffff;
		border: none;
		text-align: center;
		margin-bottom: 15px;
	}
}