@media (max-width: 1439px) {

}

@media (max-width: 1200px) {
   .container {
		max-width: 960px;
	}
}

@media (max-width: 992px) {
	.container {
      max-width: 720px;
	}
   .promo {
      height: 400px;
      width: 100%;
      &__header {
         font-size: 36px;
      }
      &__subheader {
         margin-top: 0px;
         font-size: 20px;
      }
      &__icons {
         margin-top: 35px;
         width: 355px;
      }
      &__icon {
         width: 108px;
         font-size: 12px;
      }
      &__link {
         margin-top: 14px;
         font-size: 14px;
      }
   }
   .button {
      &_main {
         margin-top: 57px;
         width: 260px;
         height: 45px;
      }
   }
   .header {
      &__logo {
         width: 200px;
      }
   }
   .carousel {
      &__inner {
         max-width: 520px;
         img {
            width: 100%;
         }
      }
   }
   .catalog {
      &__content {
         margin: 60px auto 0 auto;
         width: 650px;
      }
   }
}

@media (max-width: 768px) {
   .carousel {
      &__inner {
         max-width: 420px;
      }
      &__slick-prev &__slick-next {
         img {
            width: 10px;
            height: 30px;
         }
      }
   }
   .catalog {
      &__content {
         
         width: 300px;
      }
   }
}

